.primary_btn {
    display: block;
    width: fit-content;
    padding: 5px 10px;
    border: 2px solid #4C241D;
    border-radius: 5px;
    background-color: rgb(255, 255, 151);
    color: #4C241D;
    font-size: 16px;
    cursor: pointer;
    &:disabled {
        background-color: gray;
    }
}